        margin-bottom: 100px;

        &__p {
            margin-right: 0;
        }

        &__row {
            flex-direction: column;
        }

        &__about-us {
            width: 100%;
            padding-bottom: 148px;
            align-items: center;
        }

        &__experience {
            width: 100%;
        }
        
        &__background {
            padding: 120px 36px;
        }

       &__img {
        transform: translateY(0px);
       }
    }
}

